import React, { Component } from 'react';
import 'react-widgets/dist/css/react-widgets.css';
import {
  Button,
  Card,
  CardBody,
  CardHeader,
  Col,
  Form,
  FormGroup,
  Input,
  Label,
  Row,
  Alert
} from 'reactstrap';
import {Combobox} from 'react-widgets';
import Grid from '@material-ui/core/Grid';
import DateFnsUtils from '@date-io/date-fns';
import {
  MuiPickersUtilsProvider,
  KeyboardTimePicker,
  KeyboardDatePicker,
} from '@material-ui/pickers';
import axios from 'axios';
import { saveAs } from 'file-saver';


var keys=1;

class MakeDelivery extends Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.toggleFade = this.toggleFade.bind(this);
    this.state = {
      collapse: true,
      fadeIn: true,
      companies:[],lorries:[],places:[],rates:[],drivers:[],
      unloadingPlace:[],driver:[],lorry:[],company:[],
      timeout: 300,
      selectedDate: new Date(),
      deliveryTime: new Date(),
      paymentRate:'',deliveryDate:'',deliveryCapacity:'',deliveryDistance:'',
      showDeliNoteBtn :false,
      deliId:0,
    };
    this.onChange = this.onChange.bind(this);
    this.onSubmit =this.onSubmit.bind(this);
  }

  componentDidMount() {
    this.getCompanies();
    this.getLorries();
    //this.getUnloadingPlace();
    this.getDrivers();
    this.getDeliNote();
  }

  onChange(e) {
    
    this.setState({ [e.target.name]: e.target.value })
    
   
  }
  async onSubmit(){
    var payment
    try{
      var date = this.state.selectedDate.getFullYear() + '-' + (this.state.selectedDate.getMonth() + 1) + '-' + this.state.selectedDate.getDate();
      var time = this.state.deliveryTime.getHours() + '.' + (this.state.deliveryTime.getMinutes());
      if(this.state.deliveryDistance >= 10){
        
         payment=this.state.lorry.vehicleDeliRate * this.state.deliveryDistance ;
      }
      else{
        payment=this.state.paymentRate
      }
      
      await axios.post('/adddelivery',{
        DeliNote : this.state.deliId+1,
        Distance : this.state.deliveryDistance,
        deliveryDate : date ,
        deliveryTime : time,
        payment : payment,
        vehicleNo :this.state.lorry.vehicleNumber,
        driverLiscence :this.state.driver.driverLicenseNo,
        unloadingPlace : this.state.unloadingPlace.uploadingPlaceId
      }).then(res =>{
        var status=res.data.status;
        if(!status.localeCompare('SuccessfullyAdded')){
          alert('Successfully Added');
          this.setState({
            showDeliNoteBtn:true
          })
          this.getDeliNote();
        }
        else{
          alert('Bad Credentials')
        }
      })
    }catch(e){
      alert('Failed To Add')
    }
  }
  getCompanies(){
    fetch('/getcompanies')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        companies : json
      });
    });
    this.getUnloadingPlace()
  }

  getLorries(){
    fetch('/getlorries')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        lorries : json,
        
      });
    });
    
  }

  /*getUnloadingPlace(){
    fetch('http://localhost:4000/getunloadingplaces')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        places : json,
        
      });
    });
  }*/

  async getUnloadingPlace(){
    await axios.post('/findunloadingplaces',{
      SearchId: this.state.company.companyId
    })
    .then(res =>{
      this.setState({ 
        places : res.data,
        
      });
    });
  }
//get last delivery note
getDeliNote=async()=>{
  fetch('/getlastdeliid')
  .then(res => res.json())
  .then(json =>{
    this.setState({
      deliId:json[0].deliveryNoteNo,
    })
  });
}
  getDrivers(){
    fetch('/getdrivers')
    .then(res => res.json())
    .then(json =>{
      this.setState({ 
        drivers : json
      });
    });
  }

  getRate(){
    fetch('/getrates')
    .then(res => res.json())
    .then(json => {
      this.setState({
        rate:json
      })
    })
  }
 
  handleDateChange = date =>{
    this.setState({
      selectedDate:date
    })
  };
  handleTimeChange= time =>{
    this.setState({
      deliveryTime:time
    })
  }

  toggle() {
    this.setState({ collapse: !this.state.collapse });
  }

  toggleFade() {
    this.setState((prevState) => { return { fadeIn: !prevState }});
  }

  addDelivery=async (deliNote)=>{
    try{
      console.log(this.state.deliId)
      var date = this.state.selectedDate.getFullYear() + '-' + (this.state.selectedDate.getMonth() + 1) + '-' + this.state.selectedDate.getDate();
      var time = this.state.deliveryTime.getHours() + '.' + (this.state.deliveryTime.getMinutes());
      console.log(date)
      var payment=this.state.rate[0].ratePrice * this.state.deliveryDistance ;
      await axios.post('/adddelivery',{
        DeliNote : this.state.deliId,
        Distance : this.state.deliveryDistance,
        deliveryDate : date ,
        deliveryTime : time,
        payment : payment,
        rateId : this.state.rate[0].rateId,
        vehicleNo :this.state.lorry.vehicleNumber,
        driverLiscence :this.state.driver.driverLicenseNo,
        unloadingPlace : this.state.unloadingPlace.uploadingPlaceId
      }).then(res =>{
        var status=res.data.status;
        if(!status.localeCompare('SuccessfullyAdded')){
          alert('Successfully Added')

        }
        else{
         
          this.setState({
            showDeliNoteBtn:false
          })
        
        }
      })
    }catch(e){
      alert('Failed To Add')
    }
  }

  onClear=()=>{
    this.setState({
      paymentRate:'',
      deliveryDate:'',
      deliveryCapacity:'',
      deliveryDistance:'',
      showDeliNoteBtn:false
    })
    
  }
  onGetDeliveryNote= async()=>{
    var time = this.state.deliveryTime.getHours() + '.' + (this.state.deliveryTime.getMinutes());
     await axios.post(`/getdeliverynote`, {
       company : this.state.company.companyName,
       lorry : this.state.lorry.vehicleNumber,
       driver: this.state.driver.driverName,
       unloadingPlace: this.state.unloadingPlace.uploadingPlaceAddress,
       capacity : this.state.lorry.vehicleCapacity,
       distance : this.state.deliveryDistance,
       time : time
       })
       .then(() => axios.get('/fetch-pdf', { responseType: 'blob' }))
       .then((res) => {
         const pdfBlob = new Blob([res.data], { type: 'application/pdf' });
  
         saveAs(pdfBlob, `DeliveryNote-${this.state.lorry.vehicleNumber}.pdf`);
       })
  }

  render() {
    var {companies}=this.state;
    const selectedDate=this.state.selectedDate;
    return (
      <div className="animated fadeIn">
        <Form >
        <Row>
        <Col  xs="3" ys="6">
          </Col>
          <Col xs="12" sm="6">
            <Card>
              <CardHeader>
                <strong style={{'padding':'175px'}}>Delivery Note</strong>
                <h1>Reliable Group Lanka (Pvt) Ltd<br/></h1>
                <FormGroup style={{alignContent:'center'}}>
                <small style={{'padding':'150px'}}>N0.524, Lot 7, Hadungamuwa, Matale<br/></small>
                <small style={{'padding':'150px'}}>Tel:0765 505 252/ 0717 615 911<br/></small>
                <small style={{'padding':'150px'}}>෧බාරළු, වැලි,ගල් සැපයුම්කරු<br/></small>
                </FormGroup>
              </CardHeader>
              <CardBody style={{backgroundColor:"#d9d9d9"}}>
                
              <FormGroup row>
                    <Col md="3">
                      <Label htmlFor="company">Company</Label>
                    </Col>
                    <Col xs="12" md="9">
                    <div>
                      <Combobox
                        data={this.state.companies}
                        suggest={true}
                        //valueField='companyName'
                        textField='companyName'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            company:value
                          }, () => {
                            this.getUnloadingPlace();
                        });
                        }
                        }
                      />
                  </div>
                    </Col>
                  </FormGroup>
                <FormGroup>
                  <Label htmlFor="Driver's Name">Driver's Name</Label>
                  <div>
                      <Combobox
                        data={this.state.drivers}
                        suggest={true}
                        //valueField='companyName'
                        textField='driverName'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value)=>{
                          this.setState({
                            driver:value
                          })
                          
                        }
                        }
                      />
                  </div>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                    <Label htmlFor="Vehicle Number">Vehicle Number</Label>
                    <div>
                      <Combobox
                        data={this.state.lorries}
                        textField='vehicleNumber'
                        suggest={true}
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value) =>{
                            this.setState({
                              lorry:value,
                              deliveryCapacity:value.vehicleCapacity
                            })
                        }
                      }
                      />
                    </div>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                    <Label htmlFor="loadCapacity">Load Capacity</Label>
                    <Input type="text" name="deliveryCapacity" placeholder=" " 
                    value={this.state.deliveryCapacity} onChange={this.onChange} disabled/>
                    </FormGroup>
                  </Col>
                  </FormGroup>
                <FormGroup>
                  <Label htmlFor="address">Unloading Place</Label>
                  <div>
                      <Combobox
                        data={this.state.places}
                        suggest={true}
                        //defaultValue='Select Unloading Place'
                        valueField='uploadingPlaceAddress'
                        textField='uploadingPlaceAddress'
                        name='unloadingPlace'
                        caseSensitive={false}
                        minLength={2}
                        filter='contains'
                        onChange={(value) => {
                          
                          this.setState({
                            unloadingPlace:value
                          })
                         
                        }
                      }
                      />
                  </div>
                </FormGroup>
                <FormGroup>
                  <Label>Unloading Rate</Label>
                  <Input type="text" name="unloadingRate" value={this.state.unloadingPlace.unloadingPlaceIncomeRate} disabled/>
                </FormGroup>
                
                <FormGroup>
                  <Label htmlFor="distance">Distance</Label>
                  <Input type="text" name="deliveryDistance" placeholder="Distance (km)"
                  value={this.state.deliveryDistance} onChange={this.onChange} />
                </FormGroup>
                <FormGroup>
                {
                    this.state.deliveryDistance <= 10?
                    <FormGroup row>
                  <Col md="3">
                  <Label htmlFor="rate">Payment Rate</Label>
                  </Col>
                  <Col xs="12" md="9">
                  <Input type="text" name="paymentRate" value={this.state.paymentRate} onChange={this.onChange} />
                  </Col>
                  </FormGroup>
                    :null
                    }
                  
                </FormGroup>
                <FormGroup row>
                 
                  <MuiPickersUtilsProvider utils={DateFnsUtils}>
                    <Grid container justify="space-around">
                      <Col>
                      <KeyboardDatePicker
                        margin="normal"
                        id="date-picker-dialog"
                        label="Date"
                        format="yyyy-MM-dd"
                        value={selectedDate}
                        onChange={this.handleDateChange}
                        KeyboardButtonProps={{
                        'aria-label': 'change date',
                        }}
                      />
                      </Col>
                      <Col>
                      <KeyboardTimePicker
                        margin="normal"
                        id="time-picker"
                        label="DepartureTime"
                        value={this.state.deliveryTime}
                        onChange={this.handleTimeChange}
                        KeyboardButtonProps={{
                        'aria-label': 'change time',
                        }}
                      />
                      </Col>
                      </Grid>
                    </MuiPickersUtilsProvider>
                </FormGroup>
                <FormGroup row className="my-0">
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={()=>{this.onSubmit();}}>Save</Button>
                    </FormGroup>
                  </Col>
                  <Col xs="6">
                    <FormGroup>
                      <Button block color="dark" onClick={()=>{this.onClear();}}>clear</Button>
                    </FormGroup>
                </Col>                  
                  </FormGroup>
                  {
                    (this.state.showDeliNoteBtn==true)?
                  <FormGroup row className="my-0">
                  <Col xs="12">
                    <FormGroup>
                      <Button block color="info" onClick={()=>{this.onGetDeliveryNote();}}>Delivery Note</Button>
                    </FormGroup>
                </Col>                  
                  </FormGroup>
                  :null
                  }
              </CardBody>
            </Card>
          </Col>
        </Row>
       
        </Form>
      </div>
    );
  }
}

export default MakeDelivery;
